.default-single {
    .title-wrapper {
        background-color: $color_primary-base;
        margin: 0 0 16px 0;
        span {
            display: block;
            margin: 24px 0;
            font-family: Lato;
            font-weight: 400;
            font-size: 24px;
            color: $color_white;
            line-height: 0.6;

            height: 16px;
            background-image: url('img/icons/icon-chevron-left.svg');
            background-size: contain;
            background-repeat: no-repeat;
            background-position: left;
            padding-left: 30px;
        }
    }

    h1 {
        font-family: Lato;
        font-weight: 700;
        font-size: 24px;
        color: $color_black;
    }

    .date {
        display: block;
        font-size: 14px;
        margin: 0 0 40px 0;
        img {
            padding: 0 8px 0 1px;
        }
    }
}